.list{
    width: 300px;
    border: 1px solid #000;
    padding: 20px;
    border-radius: 4px;
    max-height: 500px;
    overflow-y: auto
}
.accordion{
border: 1px solid #000;
padding: 5px;
border-radius: 4px;
width: 100%;
}
.itemListTitle {
    font-weight: 700;
}

.item:not(:last-child){
    margin-bottom: 10px;
    
}
.list::-webkit-scrollbar {
    width: 12px;
    
}

.list::-webkit-scrollbar-track {
    border: 1px solid black;
    border-radius: 4px;
    
}

.list::-webkit-scrollbar-thumb {
    background-color: rgb(18, 18, 220);
    border-radius: 4px;
    
}


